<template>
  <div class="content">
    <div class="top">
      <div class="sign">
        <div>登录智家商城</div>
      </div>
    </div>
    <!-- 表单信息 -->
    <div class="info">
      <!-- 账号密码登录 -->
      <div v-if="show">
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
          <input type="tel" placeholder="请输入手机号码/邮箱/用户名" class="info-mm" v-model="ordinaryAccount" required @blur="count" @input="checkInputFn">
          <img class="editable-clear-x" @click="clear" :src='`${$cdnImageUrl}/login/clear.png`'>
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;" v-show="captchaShow">
          <input type="text" v-model="captcha" placeholder="请输入图形验证码" class="info-mm" @focus="verifyCount" maxlength=6 @input="checkInputFn" />
          <img class="verify-two" :src="src" alt="" @click="getPhoto">
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
          <input v-show="passwordType" type="password" v-model="password" placeholder="请输入密码" class="info-mm" @keyup.enter="passwordLogin" @focus="countM" @input="checkInputFn">
          <input v-show="!passwordType" type="text" v-model="password" placeholder="请输入密码" class="info-mm" @keyup.enter="passwordLogin" @focus="countM" @input="checkInputFn">
          <img :src="passwordType?`${$cdnImageUrl}/login/unvisible.png`:`${$cdnImageUrl}/login/visible.png`" alt="" class="show" @click="toggle">
          <div class="line"></div>
        </div>
        <div class="forget-info">
          <div @click="toRetrieve">忘记密码?</div>
        </div>
      </div>
      <!-- 短信验证码登录 -->
      <div v-else>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
          <input type="tel" :disabled="isIhaierApp" placeholder="请输入手机号" class="info-mm" v-model="mobileAccount" maxlength="13" required @input="keyup" @blur="blurFn">
          <img class="editable-clear-x" @click="clear" :src='`${$cdnImageUrl}/login/clear.png`'>
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;" v-show="captchaShow">
          <input type="text" v-model="captcha" placeholder="请输入图形验证码" class="info-mm" @focus="verifyCount" maxlength=6 @input="checkInputFn">
          <img class="verify-two" :src="src" alt="" @click="getPhoto">
          <div class="line"></div>
        </div>
        <div style="padding: 0.2rem 0.32rem 0.2rem 0;background:#fff;position: relative;height:0.56rem;">
          <!-- <input v-show="password" type="password" v-model="mm" placeholder="请输入密码" class="info-mm"> -->
          <input type="tel" v-model="messageMM" placeholder="请输入验证码" class="info-mm" @focus="safetyCount" maxlength=6 @input="checkInputFn">
          <div class="verify" v-show="!messageMMType" :style="{color:('#ccc')}">{{title}}</div>
          <div class="verify" v-show="messageMMType" @click="getMessage" :style="{color:(allow?'#2979FF':'#333')}">{{title}}</div>
          <div class="line"></div>
        </div>
      </div>
      <div class="operation">
        <button class="operation-btn" v-if="show" @click="passwordLogin" :disabled="disabledOrdinary" :class="disabledOrdinary?'btn-disabled':''">登录</button>
        <button class="operation-btn message-btn" v-else @click="passwordLogin" :disabled="disabled" :class="disabled?'btn-disabled':''">登录</button>
      </div>
      <div class="bottom" v-if="!isIhaierApp">
        <div class="operation-btn-two"  @click="change">{{text}}</div>
        <div @click="onClickRight">注册</div>
      </div>
    </div>
  </div>
</template>
<script>
import personalApi from '@/http/service/login.js';
import utils from '@/utils/utils'
import { Dialog } from 'vant';
import { queryEmployeeInfo } from '@/http/service/partners'

export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.path === '/add') {
        vm.handRouter = true
        vm.handPath = from.fullPath
      }
    })
  },
  data() {
    return {
      ordinaryAccount: '', // 普通账号（不验证手机号）
      mobileAccount: '', // 手机账号
      password: '', // 登陆密码
      messageMM: '', // 短信验证码
      passwordType: true, // 登录类型
      show: true,
      // text: '短信验证码登录',
      title: '获取验证码',
      allow: true,
      captcha: '', // 图片验证码
      src: '',
      captchaShow: false,
      phoneReg: /^1[3456789]\d{9}$/,
      toRouter: false, // 登录成功后逻辑处理，判断是否直接登录页登录
      routerName: '/',
      disabled: true, // // 手机号登录按钮验证
      disabledOrdinary: true, // 账号密码登录按钮验证
      messageMMType: false, // 控制短信验证码显隐
      timer: null, // 计时器
      replaceRouter: false, // 判断回退模式
      handRouter: false, // 手动跳转登录
      handPath: '/', // 手动登录成功跳转路径
      isIhaierApp: utils.isIhaierApp(), // ihaier环境
    }
  },
  computed: {
    callBackPath () {
      return this.$route.query.callBackPath || ''
    },
    text() {
      return this.show ? '短信验证码登录' : '账号密码登录'
    },
  },
  created() {
    if (this.isIhaierApp) {
      this.ihaierPreLogic()
    }
    this.init()
    this.$util.smMsgSdk()
    gio('track', 'LoginNamepwdBtn')
  },
  watch: {
    // mobileAccount (newValue, oldValue) {
    //   if (newValue.length > oldValue.length) {
    //     this.mobileAccount = newValue.replace(/\s/g, '').replace(/(\d{3})(\d{0,4})(\d{0,4})/, '$1 $2 $3')
    //   } else {
    //     this.mobileAccount = this.mobileAccount.trim()
    //   }
    //   if (newValue.length === 13) {
    //     this.messageMMType = true
    //   } else {
    //     this.messageMMType = false
    //   }
    // },
    // messageMM(val, old) {
    //   if (val.length !== 6) {
    //     this.disabled = true
    //   } else {
    //     this.disabled = false
    //   }
    // },
    ordinaryAccount(newValue, oldValue) {
      if (newValue === '' || this.password === '') {
        this.disabledOrdinary = true
      } else {
        this.disabledOrdinary = false
      }
    },
    password(newValue, oldValue) {
      if (newValue === '' || this.ordinaryAccount === '') {
        this.disabledOrdinary = true
      } else {
        this.disabledOrdinary = false
      }
    },

  },

  methods: {
    /**
     * ihaier逻辑
     */
    ihaierPreLogic() {
      const that = this
      this.show = false
      return utils.loadScript('//i.haier.net/public/js/qing/latest/qing.js').then(() => {
        // 获取个人信息
        if (window.qing) {
          try {
            window.qing.call('getPersonInfo', {
              success: function (res) {
                that.isSkeletonLoading = false
                if (res && res.data && res.data.userName) {
                  that.employeeNumber = res.data.userName + ''
                  if (that.employeeNumber.length === 0) {
                    that.$toast('暂未获取到员工信息，稍后重试')
                    that.closeWebView(3000)
                    return
                  }
                  // 在ihaier内保存手机号 限定登录
                  // DSG-25912 保存到session中
                  // status : 员工状态（1-在岗，2-不在岗，3-退休，4-离职，9-死亡）
                  queryEmployeeInfo({ employeeNumber: that.employeeNumber }).then(res => {
                    if (res.data.success && res.data.data && res.data.data.employee) {
                      const { mobile, status } = res.data.data
                      // if (status !== '1' && status !== 1) {
                      //   that.$toast('非在职状态，暂时无法使用')
                      //   that.closeWebView(3000)
                      //   return
                      // }
                      sessionStorage.setItem('employee_status', status)
                      sessionStorage.setItem('employee_phone', mobile)
                      that.ihaierLogic()
                      // if (mobile && mobile.length === 11) {
                      //   sessionStorage.setItem('employee_phone', mobile)
                      // } else {
                      //   that.$toast('请先在E-HR人力资源信息网维护手机号')
                      //   that.closeWebView(3000)
                      // }
                    } else {
                      that.$toast('暂未获取到员工信息，请稍后重试')
                      that.closeWebView(3000)
                    }
                  }).catch(e => console.log(e))
                  // DSG-25815 限制使用智家掌柜的员工类型
                  // const tenStart = ['88', '87', '91']
                  const eightStart = ['A', 'Z', 'a', 'z']
                  const c = i => that.employeeNumber.startsWith(i)
                  if ((that.employeeNumber.length === 8 && eightStart.some(c)) || (that.employeeNumber.length === 10)) {
                    that.$toast('暂不支持访问智家掌柜，可下载“智家掌柜”App')
                    that.closeWebView(3000)
                  }
                } else {
                  that.$toast('暂未获取到员工信息，稍后重试')
                }
              },
            })
          } catch (error) {
            that.$toast('暂未获取到ihaier员工信息，稍后重试')
          }
        } else {
          that.$toast('暂未获取到ihaier信息，稍后重试')
        }
      }).catch(e => {
        this.$toast('加载iHaier SDK失败，请更新版本后重试')
      })
    },
    /**
     * ihaier 逻辑，限制用户登录方式
     */
    ihaierLogic() {
      const that = this
      this.mobileAccount = sessionStorage.getItem('employee_phone')
      const status = sessionStorage.getItem('employee_status') + '' // 隐式类型转换
      if (this.mobileAccount && this.mobileAccount.length === 11 && status === '1') {
        this.$nextTick(() => {
          this.keyup()
          this.blurFn()
        })
      } else if (status !== '1') {
        Dialog.alert({
          message: '非在职状态，暂时无法使用',
          beforeClose(action, done) {
            if (action === 'confirm') {
              that.closeWebView(300)
            } else {
            }
            done()
          },
        });
      } else {
        Dialog.alert({
          message: '请先在E-HR人力资源信息网维护手机号',
          beforeClose(action, done) {
            if (action === 'confirm') {
              that.closeWebView(300)
            } else {
            }
            done()
          },
        });
      }
    },
    /**
     * ihaier内关闭容器
     */
    closeWebView(delay) {
      setTimeout(() => {
        if (utils.isIhaierApp() && window.qing) {
          window.qing.call('closeWebView')
        }
        if (utils.isIMaker()) {
          localStorage.clear()
          location.href = 'mallLink://closeWindow'
        }
      }, delay)
    },
    /**
   * @description  华为输入法光标错位替代
   */
    keyup() {
      let phone = this.mobileAccount
      phone = phone.replace(/[^0-9]/g, '')
      // 分组成344，并且加上' '
      var temp = []
      temp[0] = phone.slice(0, 3)
      temp[1] = phone.slice(3, 7)
      temp[2] = phone.slice(7)
      if (temp[2].length > 0) {
        temp[1] += ' '
        temp[0] += ' '
      } else if (temp[1].length > 0) {
        temp[0] += ' '
      }
      this.checkInputFn()
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
        this.verification()
      } else {
        this.messageMMType = false
      }
      this.mobileAccount = temp.join('')
    },
    blurFn() {
      this.checkInputFn()
      if (this.mobileAccount.length === 13) {
        this.messageMMType = true
        this.verification()
      } else {
        this.messageMMType = false
      }
    },

    checkInputFn() {
      if (this.show) {
        if (!this.ordinaryAccount || !this.password || (this.captchaShow && !this.captcha)) {
          this.disabledOrdinary = true
        } else {
          this.disabledOrdinary = false
        }
      } else {
        if (!this.mobileAccount || this.messageMM.length !== 6 || (this.captchaShow && !this.captcha)) {
          this.disabled = true
        } else {
          this.disabled = false
        }
      }
    },

    /**
     * @description 跳转路由处理
     */
    init() {
      this.replaceRouter = this.$store.state.needLogin
      this.routerName = this.$store.state.pathName
      if (this.$store.state.pathName === '/mine' || this.$store.state.pathName === '/cart') {
        this.password = ''
        this.messageMM = ''
        this.captchaShow = false
        // this.messageMMType = false
        if (this.mobileAccount !== '') {
          this.allow = true
          // this.show = true
          clearInterval(this.timer)
          this.title = '获取验证码'
        }
      }
    },
    /**
     * @description 返回登录前页面
     */
    goBack() {
      if (this.handRouter) {
        this.$router.replace(this.handPath)
      } else {
        this.replaceRouter ? this.$router.replace(this.routerName) : this.$store.dispatch('doHistoryBack')
      }
    },
    /**
   * @description  点击图标密码显隐
   */
    toggle() {
      this.passwordType = !this.passwordType
    },
    /**
   * @description  注册弹窗
   */
    onClickRight() {
      gio('track', 'MSignUpEntranceBtn')
      this.$router.push('/register')
    },
    /**
   * @description  登录接口
   */
    passwordLogin() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      this.password = this.password.replace(/\s/g, '')
      const that = this
      let data;
      if (!that.show) {
        gio('track', 'LmLoginBtn')
        this.$util.gioTrackWithVariable('mall_login_click_SMSLogin')
        data = {
          type: 'mobile',
          mobile: dealZh,
          regSource: 'MSmslogin',
          captcha: that.messageMM,
        }
      } else {
        gio('track', 'LnpLoginBtn')
        this.$util.gioTrackWithVariable('mall_login_click_Login')
        data = {
          type: 'pwd',
          userName: that.ordinaryAccount,
          password: encodeURIComponent(that.password),
          captcha: that.captcha,
          isNew: 1,
          noLoading: true,
          regSource: 'MUc',
        }
      }

      this.$store.dispatch('doH5Login', data).then(res => {
        if (res.code === 400) {
          if (res.errorCode === 'captcha_required') {
            this.disabledOrdinary = true
            this.disabled = true
            this.captchaShow = true
            this.captcha = ''
          }
          this.getPhoto()
          this.$toast(res.message)
        } else {
          sessionStorage.setItem('isRecordShareRelation', '') // 登录后复购分佣接口需重新调用
          if (this.callBackPath) {
            window.location.href = window.decodeURIComponent(this.callBackPath)
          } else {
            this.goBack()
          }
        }
      })
    },
    /**
   * @description  登录方式更改
   */
    change() {
      this.captcha = ''
      this.show = !this.show
      if (this.show) {
        // this.text = '短信验证码登录'
        gio('track', 'LoginNamepwdBtn')
      } else {
        // this.text = '账号密码登录'
        gio('track', 'LoginMobileBtn')
      }
      this.checkInputFn()
      if (this.captchaShow) {
        this.getPhoto()
      }
    },
    /**
   * @description  登录验证码获取接口
   */
    getMessage() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      this.$util.dealSmDeviceId().then(deviceId => {
        let data = {
          mobile: dealZh
        }
        if (this.captchaShow) {
          data.imgCaptcha = this.captcha
        }
        // 图片验证校验
        if (this.captchaShow && this.captcha === '') {
          this.$toast('请输入图片验证码')
          return
        }
        // 手机号验证
        if (!(this.phoneReg.test(dealZh))) {
          // that.$toast('手机号格式错误')
          return
        }
        if (this.allow) {
          personalApi.register(data, deviceId).then(res => {
            gio('track', 'LmSendSmsBtn')
            if (res.data.success === false) {
              this.$toast(res.data.message)
            } else {
              this.allow = false
              this.$toast('短信验证码发送成功，请及时验证！')
              // 倒计时
              let time = 60;
              this.timer = setInterval(() => {
                if (time === 0) {
                  this.allow = true
                  clearInterval(this.timer);
                  this.title = '重发验证码';
                } else {
                  this.title = '重新发送(' + time + 's)';
                  this.allow = false;
                  time--
                }
              }, 1000)
            }
          })
        }
      })
    },
    /**
   * @description  图片验证
   */
    verification() {
      const dealZh = this.mobileAccount.replace(/\s/g, '')
      if (!(this.phoneReg.test(dealZh))) {
        this.$toast('手机号格式不正确，请重新输入')
        return
      }
      gio('track', 'LmMobileInput')
      let data = {
        mobile: dealZh
      }
      personalApi.needImg(data).then(res => {
        if (res.data.data) {
          this.captchaShow = true
          this.getPhoto()
        } else {
          this.captchaShow = false
        }
      })
    },
    /**
   * @description  获取图片验证码
   */
    getPhoto() {
      let url = this.show ? '/v3/platform/web/member/getImgCaptchafromUC.html' : '/v3/platform/web/member/register/getImgCaptcha';
      this.src = this.$HOST + url + '?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue')
    },
    /**
   * @description  找回密码跳转
   */
    toRetrieve() {
      gio('track', 'FindPwdBtn')
      this.$router.push('/retrieve')
    },
    /**
   * @description  账号清除
   */
    clear() {
      this.mobileAccount = ''
      this.ordinaryAccount = ''
      this.checkInputFn()
    },
    /**
   * @description  账号输入埋点
   */
    count($event) {
      this.ordinaryAccount = this.ordinaryAccount.replace(/\s/g, '')
      gio('track', 'LnpInputName')
    },
    /**
   * @description  密码输入埋点
   */
    countM() {
      gio('track', 'LnpInputPwd')
    },
    /**
   * @description  图片验证输入埋点
   */
    verifyCount() {
      gio('track', 'LmPicInput')
    },
    /**
   * @description  短信验证码输入埋点
   */
    safetyCount() {
      gio('track', 'LmSmsInput')
    },
  },
}
</script>
